import React from 'react';

function Atividades() {
  const atividades = [
    {
      icon: 'fa-users',
      titulo: 'Reunies Mensais',
      descricao: 'Encontros regulares para troca de experincias, palestras tcnicas e confraternizao entre os associados.'
    },
    {
      icon: 'fa-graduation-cap',
      titulo: 'Cursos e Treinamentos',
      descricao: 'Preparao para exames de licena, cursos de operao, eletrnica e propagao de ondas.'
    },
    {
      icon: 'fa-trophy',
      titulo: 'Concursos e Eventos',
      descricao: 'Participao em concursos nacionais e internacionais, alm de eventos especiais de comunicao.'
    },
    {
      icon: 'fa-hands-helping',
      titulo: 'Servio Comunitrio',
      descricao: 'Apoio em emergncias, eventos pblicos e comunicaes de utilidade pblica.'
    },
    {
      icon: 'fa-satellite-dish',
      titulo: 'Estao Repetidora',
      descricao: 'Manuteno e operao de repetidoras para ampliar o alcance das comunicaes locais.'
    },
    {
      icon: 'fa-tools',
      titulo: 'Oficinas Tcnicas',
      descricao: 'Construo de antenas, manuteno de equipamentos e projetos de eletrnica aplicada.'
    }
  ];

  return (
    <section id="atividades" className="py-16 bg-gray-100">
      <div className="container mx-auto px-4">
        <h2 className="text-3xl font-bold text-center text-blue-800 mb-12">Nossas Atividades</h2>
        <div className="grid md:grid-cols-3 gap-8">
          {atividades.map((atividade, index) => (
            <div key={index} className="bg-white p-6 rounded-lg shadow-md hover:shadow-xl transition">
              <div className="text-yellow-600 text-4xl mb-4">
                <i className={`fas ${atividade.icon}`}></i>
              </div>
              <h3 className="text-xl font-bold text-gray-800 mb-3">{atividade.titulo}</h3>
              <p className="text-gray-600">{atividade.descricao}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}

export default Atividades;
