import React from 'react';

function Sobre() {
  return (
    <section id="sobre" className="py-16 bg-white">
      <div className="container mx-auto px-4">
        <h2 className="text-3xl font-bold text-center text-blue-800 mb-8">Sobre a ARCAM</h2>
        <div className="grid md:grid-cols-2 gap-8 items-center">
          <div>
            <p className="text-gray-700 mb-4">
              A Associao de Rdio Amador de Campos dos Goytacazes (ARCAM)  uma entidade dedicada 
              a promover e desenvolver o radioamadorismo na regio.
            </p>
            <p className="text-gray-700 mb-4">
              Fundada por entusiastas da comunicao via rdio, a ARCAM rene operadores licenciados 
              e interessados em aprender sobre esta fascinante atividade tcnica e social.
            </p>
            <p className="text-gray-700">
              Nossa misso  fomentar o conhecimento tcnico, a camaradagem e o servio  comunidade 
              atravs do radioamadorismo.
            </p>
          </div>
          <div className="bg-yellow-50 p-8 rounded-lg">
            <h3 className="text-xl font-bold text-blue-800 mb-4">Nossos Valores</h3>
            <ul className="space-y-3">
              <li className="flex items-start">
                <i className="fas fa-check-circle text-yellow-600 mt-1 mr-3"></i>
                <span className="text-gray-700">Educao tcnica e capacitao</span>
              </li>
              <li className="flex items-start">
                <i className="fas fa-check-circle text-yellow-600 mt-1 mr-3"></i>
                <span className="text-gray-700">Servio  comunidade</span>
              </li>
              <li className="flex items-start">
                <i className="fas fa-check-circle text-yellow-600 mt-1 mr-3"></i>
                <span className="text-gray-700">Fraternidade entre operadores</span>
              </li>
              <li className="flex items-start">
                <i className="fas fa-check-circle text-yellow-600 mt-1 mr-3"></i>
                <span className="text-gray-700">Experimentao e inovao</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Sobre;
